.wrapper-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #282a37;
    height:100vh
  }
  
.fancy-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
}
.fancy-spinner   div {
    position: absolute;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
}
.fancy-spinner div .ring {
    border-width: 0.5rem;
    border-style: solid;
    border-color: transparent;
    -webkit-animation: 2s fancy infinite alternate;
            animation: 2s fancy infinite alternate;
}      
.fancy-spinner div :nth-child(1) {
    border-left-color: #979fd0;
    border-right-color: #979fd0;
}
.fancy-spinner div :nth-child(2) {
    border-top-color: #979fd0;
    border-bottom-color: #979fd0;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
      
  
.fancy-spinner div .dot {
    width: 1rem;
    height: 1rem;
    background: #979fd0;
}

@-webkit-keyframes fancy {
    to {
      transform: rotate(360deg) scale(0.5);
    }
}

