.header-chat{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-around;
    background-color: rgb(59, 121, 150);
}

.logo__text1,.logo__text2 {
    color: #44c0c2;
    font-family: "Lobster";
    font-size: 40px; 
}
.header-btn{
    border-radius: 20px;
    border: 1px solid #f3f3f3;
    background: #f3f3f3;
    color: #2f3152;
    font-size: 12px;
    font-weight: bold;
    padding: 7px 30px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
    margin: 10px
}

.title-room{
    margin: 0 auto;
    border-radius: 22px;
    color: white;
    width: 300px;
    height: 50px;
    background-color: #4b00829c;
    padding: 10px 0;
    text-align: center;
    margin-top: 10px;
}
.chat-mess{
    display: flex;
    flex-direction: column;
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 450px;
    min-height: 450px;
}
.left-mess{
    align-self: flex-start;
    max-width: 400px;
    /* width: 100%; */
    text-align: end;
    background: #86BB71;
    padding: 5px 20px;
    border-radius: 1.125rem 1.125rem 1.125rem 0;
    margin-bottom: 5px;
}
.right-mess{
    align-self: flex-end;
    max-width: 400px;
    /* width: 100%; */
    text-align: end;
    background: #94C2ED;
    padding: 5px 20px;
    border-radius: 1.125rem 1.125rem 0 1.125rem;
    margin-bottom: 5px;
}
.send-mess{
    max-width: 1100px;
    width: 100%;
    margin: 0 auto;
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
}
.send-mess_input{
    width: 70%;
}
.send-mess_btn{
    border-radius: 20px;
    border: 1px solid #f3f3f3;
    background: #f3f3f3;
    color: #2f3152;
    font-size: 12px;
    font-weight: bold;
    padding: 6px 30px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
    margin: 0 30px;
}
.send-mess_btn:hover{
    background-color: #94C2ED;
}

@media screen and (max-width:501px){
    .logo2{
        display: none;
    }
    .logo1{
        align-self: flex-end;
    }
    .logo__text1{
        font-size: 20px;
    }
    .chat-mess{
        max-height: 400px;
        min-height: 400px;
    }
    .left-mess{
        margin-left: 10px;
    }
    .right-mess{
        margin-right: 10px;
    }
